"use server"

import clientPromise from "@/lib/mongodb";

export async function POST(request) {
    const body = await request.json();
    const clint = await clientPromise;
    const db = clint.db("Linktree");
    const collection = db.collection("Login");

    const doc  = await collection.findOne({username: body.username, email: body.email, password: body.password})

    if(body.username === doc.username || body.email === doc.email || body.password === doc.password){
      return Response.json({success:'true', error:'false', message: 'Already exist this username pleace login', result: null })
      console.log("the result is", doc)
    }
    else{
        return Response.json({success:'false', error:'true', message: 'nothing', result: null })
        console.log("the result is", doc)
    }
  }